import React from 'react';
import './Categories.css';

import { connect } from 'react-redux';
import { showCategory } from '../../actions';
import {Link} from 'react-router-dom';

import {Container, Row, Col} from 'react-bootstrap';


function containsObject(list, target){
    for(let i=0; i<list.length; i++){
        if(list[i][0] === target){
            return true;
        }
    }
    return false;
}

class CategoryList extends React.Component{

    renderCategory = () => {
        
        const categories_id = [];
        //getting categories id from all products
        this.props.categories.forEach(product => {
            if(!(containsObject(categories_id, product.category_name))){
                categories_id.push([product.category_name, product.category_image, product.category]);
            }
        });

        return categories_id.map(ele => {
            console.log(`../images/${ele[1]}`);

            return (
                    <div className="card" key={ele[0]}>
                        <div className="image">
                            
                            <img src={require(`../images/${ele[1]}`)} height="250px" width="300px" alt="fruits" />

                        </div>
                        <div className="content">
                            <div className="header">                        
                                {"Category: "+ ele[0]}
                            </div>
                            <div className="extra content">
                                <div className="ui buttons">
                                    <Link to="/ecommerce/product">
                                        <button className="button"
                                        onClick={() => this.props.showCategory(ele[2])}
                                        >
                                        Show
                                        </button>
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                );
        });

    };
    
    render(){
        return (
            <Container>
                <Row className="categoryList">
                    <Col>
                        <div className="ui cards">
                        {
                            this.renderCategory()
                        }
                        </div>
                    </Col>
                </Row>
                
                <div className="cart-icon">
                    <Link to="/ecommerce/showCart/">
                        <img src={require("../images/shopping-cart.png")} height="32px" alt="Cart button"/>
                    </Link>
                </div>
            </Container>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        categories: state.products,
    }
};

export default connect(mapStateToProps, {showCategory})(CategoryList);