import React from 'react';
import { connect } from 'react-redux';
import {Link} from 'react-router-dom';

import {showProductDetails} from '../../actions';

import './Product.css';

import {Container, Row, Col} from 'react-bootstrap';

const Products = (props) => {

    const [sortState, setSortState] = React.useState(false);
    const [filterCategory, setFilterCategory] = React.useState(0);
    const [searchWord, setSearchWord] = React.useState("");

    const sortAscending = (products) => {
        products.sort((a,b) => {
            if(a.price < b.price) return -1;
            if(a.price > b.price) return 1;
            return 0;
        });
        return productListRender(products, props.category, props.showProductDetails);
    };

    
    const sortDescending = (products) => {
        products.sort((a,b) => {
            if(a.price < b.price) return 1;
            if(a.price > b.price) return -1;
            return 0;
        });
        return productListRender(products, props.category, props.showProductDetails);
    };

    //filter function before rendering
    const filteringFunction = (category) => {
        
        const deepCopyState = props.products.map(item => {
            return {...item}
        });

        if(category === 1){
            return deepCopyState.filter(ele => ele.price <= 300);
        }
        if (category === 2){
            return deepCopyState.filter(ele => ele.price > 300);
        }
        return deepCopyState;
    }

    //filtering on searchword
    const filterOnWord = (products) => {
        if(searchWord === ""){
            return products;
        }

        return products.filter(product => product.product_title.includes(searchWord));

    }

    //utility function
    const returnElement = () =>{

        const auxCopyState = filterOnWord(filteringFunction(filterCategory));

        
        
        if(sortState){
            return sortAscending(auxCopyState);
        }
        return sortDescending(auxCopyState);
    }

    return (
    <Container className="products" fluid>
        <Row>
            <Col xs={5} md={3} className="functions">
                <Row className="my-3">
                    <Col>
                        <Row><Col><h4>Sort on Price</h4></Col></Row>
                        <Row><Col><button className="button" type="button" onClick= {() => setSortState(true)}>low to high</button></Col></Row>
                        <Row><Col><button className="button" type="button" onClick= {() => setSortState(false)}>high to low</button>    </Col></Row>
                    </Col>
                </Row>
                
                <Row>
                    <Col>
                        <Row><Col><h4>Filter on Price</h4></Col></Row>
                        <Row><Col><button className="button" type="button" onClick = {() => setFilterCategory(1)}>Price &lt;= Rs 300</button></Col></Row>
                        <Row><Col><button className="button" type="button" onClick = {() => setFilterCategory(2)}>Price &gt; Rs 300</button></Col></Row>
                        <Row><Col><button className="button" type="button" onClick = {() => setFilterCategory(0)}>No Filter</button></Col></Row>
                    </Col>
                </Row>

                <Row className="my-3 searchBar">
                    <Col>
                        <Row><Col><h4>Search Product by title</h4></Col></Row>

                        <Row>
                            <Col>
                                <input type="text" id="textBar"/>
                                <button className="button" 
                                onClick={() => setSearchWord(document.getElementById("textBar").value)} 
                                >Search
                                </button>
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </Col>

            <Col xs={7} className="productList">
                <div className="ui cards">{returnElement()}</div>
            </Col>
        </Row>

        <div className="cart-icon">
            <Link to="/ecommerce/showCart/">
                <img src={require("../images/shopping-cart.png")} height="32px" alt="Cart button"/>
            </Link>
        </div>
    </Container>
    );
}

const mapStateToProps = (state) => {

    if(state.showCategory){    
        localStorage.setItem("showCategory", state.showCategory);
    }
    return {
        products: state.products,
        category: state.showCategory || parseInt(localStorage.getItem("showCategory"))
    }
};

export default connect(mapStateToProps,{showProductDetails})(Products);



const productListRender = (products, category, showProductDetails) => 
    {
        return products.map(product => {
        if(product.category === category){
            console.log();
            return (
                    <div className="product">
                            <Link key={product.product_id} to="/ecommerce/product/detail" onClick= {() => showProductDetails(product.product_id)}>
                            <img 
                            src={require(`../images/${product.product_image}`)} 
                            alt="product view" 
                            height="100px"
                            width="100px"
                            />
                            </Link>
                            <span className="header">
                                Title: {product.product_title}
                            
                            </span>
                            <span className="price">
                                Price: Rs {product.price}
                            </span>
                    </div>

                );
            }
            return <div key={product.product_id}></div>;
        });
    }